@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');

:root {
    --disc-width: 400px;
    --disc-height: 400px;
}

.container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 1200px;
    height: 1200px;
    margin: auto;
}

.cover {
    background: #f1f1ef;
    width: var(--disc-width);
    height: var(--disc-height);
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
}

.box-container {
    position: relative;
}

.box {
    position: absolute;
    z-index: 3;
    opacity: 0.4;
    border-radius: 1%;
}

.box.one {
    background-color: #8cb582;
    width: 120px;
    height: 150px;
    top: -140px;
    left: 37px;
}

.box.two {
    background-color: #d4795e;
    width: 61px;
    height: 61px;
    top: 25px;
    left: 63px;
}

.box.three {
    background-color: #7071a7;
    width: 215px;
    height: 42px;
    top: -106px;
    left: 118px;
}

.box.four {
    background-color: #eede4f;
    opacity: 0.2;
    width: 165px;
    height: 125px;
    top: -35px;
    left: 175px;
}

.box.five {
    background-color: #887591;
    width: 85px;
    height: 165px;
    top: -27px;
    left: 220px;
}

.box-line {
    width: calc(var(--disc-width) - 18px);
    height: calc(var(--disc-height) - 18px);
    border: solid 1px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

p {
    font-family: 'Oswald', sans-serif;
    text-transform: uppercase;
    font-size: 5px;
    letter-spacing: 5px;
    font-style: italic;
    font-weight: bold;
}

#disc-artist {
    position: relative;
    left: 58px;
    top: -5px;
}

#disc-title {
    position: relative;
    left: -56px;
    top: -1px;
}

.image {
    background-image: url("../assets/img/shapeshifter-image.png");
    background-size: cover;
    width: calc(var(--disc-width) / 2);
    height: calc(var(--disc-height) / 2);
}

.disc {
    background:
        linear-gradient(
            332deg,
            rgba(146, 190, 134, 1) 0%,
            rgba(255, 255, 255, 1) 49%,
            rgba(146, 190, 134, 1) 100%
        );
    width: var(--disc-width);
    height: var(--disc-height);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

/*.cover:hover + .disc {
    transform: translateX(calc(var(--disc-width) * -1)) rotate(-80deg);
}*/

.disc.translate {
    transition: transform 2s linear;
    transform: translateX(calc(var(--disc-width) * -1)) rotate(-80deg);
}

.line {
    position: absolute;
    width: calc(var(--disc-width) - 30px);
    height: calc(var(--disc-height) - 30px);
    border: solid 1px rgba(0, 0, 0, 0.05);
    border-radius: 50%;
}

.line.two {
    width: calc(var(--disc-width) - 60px);
    height: calc(var(--disc-height) - 60px);
}

.line.three {
    width: calc(var(--disc-width) - 130px);
    height: calc(var(--disc-height) - 130px);
}

.line.four {
    width: calc(var(--disc-width) - 160px);
    height: calc(var(--disc-height) - 160px);
}

.ticket {
    --ticket-width: calc(var(--disc-width) / 3);
    --ticket-height: calc(var(--disc-height) / 3);

    background: #dad9df;
    width: var(--ticket-width);
    height: var(--ticket-height);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hole {
    background: white;
    width: calc(var(--ticket-width) / 13);
    height: calc(var(--ticket-height) / 13);
    border-radius: 50%;
}
